import { Component, Injector, OnInit } from '@angular/core';
import { FormArray, FormGroup } from '@angular/forms';
import { URLz } from 'src/app/enums/url.enum';
import { BankCustomization } from 'src/app/interface/customizations/bank-customization';
import { BaseForm } from 'src/app/class/base.form';
import { PartialSubmit } from 'src/app/interface/common/partial-submit';

@Component({
  selector: 'app-bank-add',
  templateUrl: './bank-add.component.html',
  styleUrls: ['./bank-add.component.css']
})
export class BankAddComponent extends BaseForm  implements OnInit {
  constructor(injector: Injector) {
    super(injector);
  }
  ngOnInit() {
    this._fhs._pathLocation = '/bank/bank_list';
    this.param.endpoint = URLz.BANK_CUSTOM;
    this.initForm();
    this._activeId = this._fhs._getURLParam('id');
    if (this._activeId) {
      this.patchData();
    }
  }
  initForm() {
    this._fs._form = this._fb.group({
      bank_name:[''],
      account_number:[''],
      account_title:[''],
      branch_name:[''],
      bank_code:[''],
      address:[''],
      gl_code:[''],
      account_type: ['',this._vs._val('Account Type', { isField: 0 })],
      bg:[],
      le:[],
      customization: this._fb.array(
        [this.customization()],
        [this._vs.repeatTwoFields('organisation_id', 'system_id')]
      )
    });
  }
  data: any
  patchData() {
    this._http.get({ ...this.param, resource: this._activeId }).subscribe((res: any) => {
      const data : BankCustomization  = this.data = res.data.row;
      this._fs._form.patchValue({
        bank_name:data?.name,
        account_number:data?.account_number,
        account_title:data?.account_title,
        branch_name:data?.branch,
        bank_code:data?.bank_code,
        address:data?.address,
        gl_code:data?.gl_code,
        bg:data?.bg,
        le:data?.le,
        account_type: data?.account_type,
      });
      if (data.customization.length > 0) {
        this.patchCustomization(data.customization);
      }
      if (data.account_type != '') {
        this._fs._form.markAllAsTouched();
      }
    });


  }
  // 1 Customization Initialization
  customization( organization = '', system = '' ): FormGroup {
    return this._fb.group({
      organisation_id: [organization, this._vs._val('Organization', {isField:0})],
      system_id: [system, this._vs._val('System', {isField:0})],
    });
  }
  // 2 Customization (Template Iteration Purpose)
  get customizations() {
    return this._fs._form?.get('customization') as FormArray;
  }
  // 3 Customization (Adding New)
  addCustom() {
    const custom = <FormArray>this._fs._form.get('customization');
    custom.push(this.customization());
  }
  // 4 Customization (Removing Selected)
  rmvCustom(index: number) {
    const CustomizationArr = <FormArray>this._fs._form.get('customization');
    CustomizationArr.removeAt(index);
  }
  // 5 Customization (Updating Data)
  patchCustomization(
    customization: {
      organisation_id: string;
      system_id: string;
    }[]
  ) {
    const formArray = this._fs._form.get('customization') as FormArray;
    if (customization.length > 0) formArray.clear();
    customization.forEach((custom) => {
      formArray.push(
        this.customization(
          custom.organisation_id,
          custom.system_id,
        )
      );
    });
  }

  _onSubmity(ps: PartialSubmit = this._fss.defaultBehaviour) {
    super._onSubmity({
      body: (ps:PartialSubmit) => {
        ps.param.body = this._fs._form.getRawValue();
      }
    })
  }
}
