import { Component, Injector, OnInit } from '@angular/core';
import {MatDialogConfig } from '@angular/material/dialog';
import { URLz } from 'src/app/enums/url.enum';
import { MatFullDialogComponent } from 'src/app/shared/components/dialogs/mat-full-dialog/mat-full-dialog.component';
import { BaseListClass } from 'src/app/class/base-list-class';
@Component({
  selector: 'app-bank-list',
  templateUrl: './bank-list.component.html',
  styleUrls: ['./bank-list.component.css']
})
export class BankListComponent extends BaseListClass implements OnInit {

  constructor(public injector: Injector) {
    super(injector);
  }

  ngOnInit() {
    this._pathLocation = '/bank/bank_add';
    this._component = 'Banks';
    this.initTables();
  }

  initTables(){
    this._reset(URLz.BANK_CUSTOM)
    this._tbls[URLz.BANK_CUSTOM].columns = [
      'id',
      'actions',// 'customization',
      'account_number',
      'account_title',
      'branch',
      'bank_code',
      'gl_code',
      'account_type'
    ];
    this._tbls[URLz.BANK_CUSTOM].endpoint = URLz.BANK_CUSTOM
    this._formCreator(this._tbls[URLz.BANK_CUSTOM])
    this._refresh(URLz.BANK_CUSTOM)
  }

  openDialog(data, title) {
    const config: MatDialogConfig = {
      panelClass: "dialog-responsive",
      data:  {source: data, title: title}
    }
    const dialogRef = this._dialog.open(
      MatFullDialogComponent, config);
    dialogRef.afterClosed().subscribe(() => {return});
  }


}
